body {
  background: #333333;
  color: #ffffff;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.weather-container {
  background: transparent;
  color: #fff;
  transition: background 0.5s ease;
  min-height: 100vh;
}

@media only screen and (min-width : 1200px) {
	.weather-forecasts {
		display: flex;
    flex-wrap: wrap;
	}
}

/* Night and Day */

.night-bg {
  background: #0e2244;
}

.night-color {
  color: #001942;
}

.day-bg {
  background: #eff5ff;
  color: #0e2244;
}

.day-color {
  color: #68b1ff;
}
.location-box-wrapper {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2000000000;
  padding: .75em 1em;
}

.location-suggestions-wrapper {
  position: relative;
}

.location-suggestions {
  position: absolute;
  background: rgba(255, 255, 255, .95);
  box-shadow: 0 .125em .25em 0 rgba(75, 75, 75, .1);
  border-radius: .25em;
  border-color: #ececec #ececec #cecece #ececec;
  color: #333333;
  width: 100%;
  font-weight: 600;
  margin-top: .5em;
}

.location-suggestion {
  margin: 0 1em;
  padding: .75em 0;
  border-top: solid 1px #dedede;
  border-collapse: separate;
}

.location-suggestion:first-child {
  border-top: 0;
}

.location-box {
  background: #ffffff;
  color: #333333;
  box-shadow: 0 .125em .25em 0 rgba(75, 75, 75, .1);
  border-radius: .25em;
  border-color: #ececec #ececec #cecece #ececec;
  border-style: solid;
  border-width: 1px;
  max-height: 100vh;
  font-size: 1.5em;
  padding: .5em .5em;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.location-box.editing {
  background: #fefefe;
  color: #0e2244;
  border-color: #efefef #efefef #dddddd #efefef;
}

.location-box-wrapper.night > .location-box.editing {
  background: rgba(255, 255, 255, .9);
  color: #000;
}

.location-icon {
  cursor: pointer;
}

.location-icon > img {
  height: 1.5em;
}

.location-string {
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}

.location-search-field {
  background: transparent;
  border: 0;
  font-size: 1em;
  flex-grow: 1;
  padding: 0;
  margin: 0;
  outline: none;
  display: flex;
  flex-shrink: 1;
  color: inherit;
  text-transform: capitalize;
}

.edit-search-field {
  cursor: pointer;
}
/* Current Weather */

.pending, .error {
  text-align: center;
  margin: 1em 0;
}

/* Current Weather Description */

.current-weather-description {
  margin: .5em 1em;
  padding: 0;
  font-size: 1.5em;
  text-transform: capitalize;
  flex-basis: 100%;
}

/* Current Weather Icons */

.current-weather-icons {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.current-weather-icons > div {
  flex-basis: 50%;
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.current-weather-icons > div > svg {
  width: 90%;
  max-width: 75vw;
  max-height: 35vh;
}

/* Current Weather Temperature */

.current-weather-temp {
  margin: 0 1em;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
}

.current-weather-temp > .temp-live {
  font-size: 6.5em;
}

.current-weather-temp > .temp-hi-lo {
  font-size: 2em;
  display: flex;
  flex-direction: column;
  align-content: stretch;
}

.temp-hi, .temp-lo {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width : 600px) {
  .current-weather {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .current-weather-icons {
    flex-basis: 50%;
    flex-grow: 1;
  }

  .current-weather-temp {
    align-self: stretch;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    font-size: 1.25em;
  }

  .temp-hi-lo {
    flex-direction: row;
    align-content: center;
  }

  .temp-hi, .temp-lo {
    justify-content: center;
  }

  .current-weather-description {
    flex-grow: 1;
    font-size: 2.15em;
  }
}

@media only screen and (min-width : 1200px) {
  .current-weather {
    flex-basis: 30%;
    flex-shrink: 0;
  }
}
.temp {
  display: flex;
  padding: 0;
  margin: 0;
  line-height: 1em;
  cursor: pointer;
}

.temp-unit {
  font-size: .75em;
}
.WeatherIcon {
	width: 100%;
	fill: #ffffff;
}

.day-bg .Sun {
	fill: #0e2244;
}
.Sun {
	-webkit-transform-origin: 50% 50%;
	-webkit-animation-name: spin;
    -webkit-animation-duration: 30000ms;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;

    -moz-transform-origin: 50% 50%;
    -moz-animation-name: spin;
    -moz-animation-duration: 30000ms;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: linear;

    -ms-transform-origin: 50% 50%;
    -ms-animation-name: spin;
    -ms-animation-duration: 30000ms;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: linear;

    transform-origin: 50% 50%;
    animation-name: spin;
    animation-duration: 30000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}
@-webkit-keyframes spin { 
    from { 
        -webkit-transform: rotate(0deg); 
    } to { 
        -webkit-transform: rotate(360deg); 
    }
}
@keyframes spin { 
    from { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } to { 
        -webkit-transform: rotate(360deg); 
                transform: rotate(360deg); 
    }
}
.Moon .outline {
	fill: #000000;
}

.Moon .star {
	fill: #000000;
}

.night-bg .Moon .outline,
.night-bg .Moon .star {
	fill: #ffffff;
}
.Cloud .outline {
	fill: #000000;
}

.night-bg .Cloud .outline {
	fill: #ffffff;
}

.night-bg .Cloud .fill {
	fill: #dddddd;
}

.night-bg .Cloud .outline {
	fill: #ffffff;
}

.Cloud.DarkCloud .outline {
	fill: #000000;
}

.Cloud.DarkCloud .fill {
	fill: #646464;
}

.night-bg .Cloud.DarkCloud .outline {
	fill: #dddddd;
}

.Cloud.Drizzle .fill {
	fill: #cccccc;
}
.Rain {
	fill: #000000;
}

.night-bg .Rain {
	fill: #ffffff;
}
.Lightning {
	fill: #000000;
}

.night-bg .Lightning {
	fill: #ffffff;
}
.Snow .flake .outline {
	fill: #000000;
}

.Snow .flake .fill {
	fill: #ffffff;
}

.night-bg .Snow .flake .outline {
	fill: transparent;
}
.Mist path {
	opacity: .75;
}
.forecast-weather {
}

.pending, .error {
  text-align: center;
  margin: 1em 0;
}

.forecast-weather-day {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: .25em .75em;
  font-size: 1.5em;
}

.forecast-weather-day:nth-child(2n) {
  background: #f7faff;
  border-width: 1px 0 1px 0;
  border-style: solid;
  border-color: #ececec;
}

.night-bg .forecast-weather-day:nth-child(2n) {
  background: #000c23;
  border-color: #494949;
}

.forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
  margin: 0 .25em;
}

.forecast-temperature {
  flex-shrink: 0;
  flex-basis: 15%;
}

.forecast-day-description {
  flex-grow: 1;
  font-size: .9em;
  text-transform: capitalize;
}

.forecast-day-icon {
  flex-basis: 15%;
  flex-grow: 0;
  /*background: pink;*/
}

.forecast-weekday {
  flex-basis: 15%;
  flex-shrink: 0;
}

.forecast-day-icon svg {
  height: 2em;
}

@media only screen and (min-width : 600px) {
  .forecast-weather {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .forecast-weather-day {
    background: rgba(255, 255, 255, .01);
    flex-basis: 25%;
    padding: 0;
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  }

  .forecast-weather-day:nth-child(2n) {
    border-width: 0;
  }

  .forecast-temperature, .forecast-day-description, .forecast-day-icon, .forecast-weekday {
    padding: 0 .25em;
  }

  .forecast-temperature {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .forecast-day-description {
    flex-grow: 1;
    font-size: 1em;
    margin: .25em 0;
    text-align: center;
  }

  .forecast-day-icon {    
    flex-basis: auto;
    text-align: center;
    margin: .25em 0;
  }

  .forecast-day-icon > svg {
    width: 50%;
    height: 100%;
  }

  .day-bg .forecast-weekday {
    background: rgb(14, 34, 68, .9);
    color: #ffffff;
  }

  .forecast-weekday {
    background: rgba(255, 255, 255, .8);
    color: #0e2244;
    margin: 0px;
    font-size: 1.25em;
    flex-basis: auto;
  }

  .forecast-temperature {
    display: flex;
    flex-basis: auto;
    margin: .25em 0;
  }
}

@media only screen and (min-width : 900px) {
  .forecast-weather-day {
    flex-basis: 33.33%;
  }
}

@media only screen and (min-width : 1200px) {
  .forecast-weather {
    padding: 1em;
  }

  .forecast-weather-day {
    flex-basis: 12.5%;
  }
}

@media only screen and (min-width : 1800px) {
  .forecast-weather-day {
    flex-basis: 12.5%;
  }
}
.HourlyWeather {
	background: #fcfcfc;
	overflow-x: scroll;
	-webkit-overflow-scrolling: touch;
	overflow-scrolling: touch;
	display: flex;
	flex-direction: row;
	border-width: 1px 0;
	border-style: solid;
	border-color: #cecece;
	padding: .25em 0;
}

.night-bg .HourlyWeather {
	background: #000c23;
	border-color: #dedede;
}

.HourlyWeather>:first-child {
	padding-left: 1em;
}

.HourlyWeather>:last-child {
	padding-right: 1em;
}

.forecast-weather-hourly {
	flex-basis: 14%;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	text-align: center;
	margin: .5em .25em;
}

.forecast-time {
}

.forecast-hourly-temperature {

}

.forecast-hourly-icon {
	
}

.forecast-hourly-description {
	font-size: .8em;
	flex-grow: 1;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	text-transform: capitalize;
}

@media only screen and (min-width : 1200px) {
	.HourlyWeather {
		overflow: visible;
		flex-wrap: wrap;
		background: transparent;
		border: 0;
		align-items: space-around;
		flex-basis: 70%;
	}

	.forecast-time, .forecast-hourly-temperature {
		font-size: 1.5em;
	}

	.forecast-hourly-description {
		font-size: 1.125em;
	}

	.night-bg .HourlyWeather {
		background: transparent;
	}

	.HourlyWeather>:first-child {
		padding: 0;
	}

	.HourlyWeather>:last-child {
		padding: 0;
	}

	.forecast-weather-hourly {
		flex-basis: 16.66%;
		flex-grow: 0;
		margin: 0;
		flex-shrink: 0;
		flex-grow: 0;
	}
}
